import React, { Component } from 'react';
import Social from './Social';

class About extends Component {
    render() {
        return (
            <div className="container">
                <h3 className="hr"><span className="hr">SOBRE</span></h3>
                <p>Oi, sou desenvolvedor web a 8 anos, especialista na linguagem PHP e frameworks como Zend e Laravel.
                    Tenho uma empresa com a qual presto servios em desenvolvimento Web e Mobile.</p>
                <p>Em 2009 comecei o curso de Anlise e Desenvolvimento de Sistemas e no ano seguinte encontrei a
                    primeira oportunidade na rea. No decorrer dos anos trabalhei com desenvolvimento de sistemas,
                    e-commerce e Mobile.</p>
                <p>Atualmente tenho focado no estudo de programao reativa com ReactJS e VueJS. Acredito que essas
                    ferramentas podem me tornar mais produtivo e proporcionar uma experincia de uso melhor para os
                    usurios.</p>
                <Social />
            </div>
        );
    }
}

export default About;


